import { Icon } from '@iconify/react';
import { motion } from 'framer-motion';
import PaperIcon from '@material-ui/icons/Description';
import DonationIcon from '@material-ui/icons/VolunteerActivism';
// material
import { experimentalStyled as styled } from '@material-ui/core/styles';
import { Button, Box, Link, Container, Typography, Stack, StackProps, } from '@material-ui/core';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
//
import { varFadeIn, varFadeInUp, varWrapEnter, varFadeInRight } from '../../animate';

// ----------------------------------------------------------------------

const RootStyle = styled(motion.div)(({ theme }) => ({
  position: 'relative',
  backgroundColor: theme.palette.grey[400],
  [theme.breakpoints.up('md')]: {
    top: 0,
    left: 0,
    width: '100%',
    height: '100vh',
    display: 'flex',
    position: 'relative',
  }
}));

const ContentStyle = styled((props: StackProps) => <Stack spacing={5} {...props} />)(
  ({ theme }) => ({
    zIndex: 10,
    
    margin: 'auto',
    textAlign: 'center',
    position: 'relative',
    paddingTop: theme.spacing(15),
    paddingBottom: theme.spacing(15),
    [theme.breakpoints.up('md')]: {
      margin: 'unset',
      textAlign: 'left'
    }
  })
);

const HeroOverlayStyle = styled(motion.img)({
  zIndex: 9,
  width: '100%',
  height: '100%',
  objectFit: 'cover',
  position: 'absolute'
});

const HeroImgStyle = styled(motion.img)(({ theme }) => ({
  top: 0,
  right: 0,
  bottom: 0,
  zIndex: 9,
  width: '100%',
  margin: 'auto',
  position: 'absolute',
  [theme.breakpoints.up('md')]: {
    top: '-90px',
    right: '2%',
    width: 'auto',
    height: 'auto'
  },
  [theme.breakpoints.up('lg')]: {
    top: '-90px',
    right: '18%',
    width: 'auto',
    height: 'auto'
  }
}));

// ----------------------------------------------------------------------

export default function LandingHero() {
  return (
    <>
      <RootStyle initial="initial" animate="animate" variants={varWrapEnter}>
        <HeroOverlayStyle alt="overlay" src="/static/img/grdnt_bg.jpg" variants={varFadeIn} />

        <HeroImgStyle alt="hero" src="/static/img/institution_img.png" variants={varFadeInUp} className="hero_img" />

        <Container maxWidth="lg">
          <ContentStyle>
            <motion.div variants={varFadeInRight}>
              <Typography variant="h1" sx={{ color: 'common.white' }} className="bnnr_h1">
              Institution
              </Typography>
            </motion.div>

            <motion.div variants={varFadeInRight}>
              <Typography sx={{ color: 'common.white', }} className="bnnr_p bnnr_p_v2" >
              Meet the team that is running the TreeDefi project and the future companies to come.
              </Typography>
            </motion.div>

            <motion.div variants={varFadeInRight}>
              <Button
                size="large"
                variant="contained"
                target="_blank"
                component={Link}
                href="https://calendly.com/treedefi/istitution"
                sx={{ marginRight: '25px' }}
                className="bnr_btn bnr_btn_wt"
              >
                Discover More
              </Button>
              
            </motion.div>

            
          </ContentStyle>
        </Container>
      </RootStyle>
      {/* <Box sx={{ height: { md: '100vh' } }} /> */}
    </>
  );
}
